<template>
    <div class="register">
        <div class="container">
            <div class="copyWriting">
                <p>人生 每一个清晨<br>都该努力</p>
                <span>看见初升的太阳</span>
                <div class="have" @click="href">已有账号?</div>
            </div>
            <div class="form">
                <p>用户注册</p>
                <el-form status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm" :model="ruleForm">
                    <el-form-item prop="user">
                        <el-input type="text" prefix-icon="el-icon-user" v-model="ruleForm.user" placeholder="请输入用户名"></el-input>
                    </el-form-item>
                    <el-form-item prop="pass">
                        <el-input type="password" prefix-icon="el-icon-lock" v-model="ruleForm.pass" placeholder="请输入密码"></el-input>
                    </el-form-item>
                    <el-form-item prop="checkPass">
                        <el-input type="password" prefix-icon="el-icon-lock" v-model="ruleForm.checkPass" placeholder="请输入确认密码"></el-input>
                    </el-form-item>
                    <el-form-item class="btn_box">
                        <el-button type="primary" @click="submit('ruleForm')">提交</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>
<script>
import registerApi from '../../api/register'
export default {
    data() {
        // 自定义验证规则
        let validatePass = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请输入密码'));
            }else {
                if (this.ruleForm.checkPass !== '') {
                    this.$refs.ruleForm.validateField('checkPass');
                }
                callback();
            }
        };
        let validatePass2 = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('请再次输入密码'));
            } else if (value !== this.ruleForm.pass) {
                callback(new Error('两次输入密码不一致!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm:{
                user: '',
                pass: '',
                checkPass: ''
            },
            msg:'',
            timer:null,
            rules: {
                user: [
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                ],
                pass: [
                    { validator: validatePass, trigger: 'blur' },
                    { min: 11, max: 16, message: '长度在 11 到 16 个字符', trigger: 'blur' }
                ],
                checkPass: [
                    { validator: validatePass2, trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        beforeDestroy() {
            clearTimeout(this.timer)
        },
        submit(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    registerApi.register(this.ruleForm).then(data => {
                        if(data.status === 200){
                            this.msg = `3秒后即将跳转登录页面`
                            this.notice(data.data)
                            this.resetForm(formName)
                            this.timer = setTimeout(() => {
                                this.$router.push('/login')
                            }, 3000);
                        }
                        if(data.status === 202){
                            this.msg = `请重新选择用户名`
                            this.notice(data.data)
                        }
                    })
                } else {
                    return false;
                }
            });
        },
        notice(title) {
            this.$notify({
                title: title,
                message: this.msg,
                type: 'success'
            })
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 路由跳转
        href(){
            this.$router.push('/login')
        }
    },
}
</script>
<style scoped>
    .register{
        position: relative;
        width: 100%;
        height: 100%;
        background-image: url('../../assets/bg.jpg');
        background-size: cover;
    }
    .container{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 740px;
        height: 500px;
    }
    .container>.copyWriting{
        position: relative;
        width: 460px;
        height: 100%;
        float: left;
        box-sizing: border-box;
        padding: 30px;
        background-color: rgba(255,255,255,0.3);
    }
    .copyWriting>p,
    .copyWriting>span{
        font-weight: bold;
        color: rgb(133,122,104);
    }
    .copyWriting>p{
        font-family: monospace;
        white-space: pre;
        font-size:40px;
    }
    .copyWriting>.have{
        position: absolute;
        bottom: 10px;
        right: 20px;
        font-size: 14px;
        cursor: pointer;
        color: rgb(0, 0, 238);
    }
    .container>.form{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 280px;
        height: 100%;
        float: right;
        background-color: #fff;
    }
    .form>p{
        width: 100%;
        padding-left: 30px;
        box-sizing: border-box;
        margin: 15px;
        color: #C1C1C1;
    }
    .el-form-item>>>.el-input__inner{
        border-radius: 0;
        border: 1px solid #C1C1C1;
    }
    .el-form-item>>>.el-form-item__content{
        width: 100%;
    }
    .el-button{
        border-radius: 0;
        width: 100%;
        margin-top: 10px;
        border-color: rgb(23,131,144);
        background-color: rgb(23,131,144);
    }
    /* .el-button--primary:hover {
        border-color: rgb(#388E8E) !important;
        background-color: rgb(#388E8E);
    } */
    .btn_box{
        display: flex;
        justify-content: center;
    }
</style>