<template>
    <div class="header">
        <div class="login_register">
            <div class="contniear">
                <p class="name fl">欢迎光临~{{'邓草草'}}</p>
                <p class="lr fr">
                    <router-link to="/login" tag="span">
                        <i class="iconfont icon-denglu"></i>
                        登录
                    </router-link>
                    <router-link to="/register" tag="span">
                        <i class="iconfont icon-zhuce"></i>
                        注册
                    </router-link>
                </p>
            </div>
        </div>
        <div class="nav">
            <div class="nav_contniear">
                <p class="logo">
                    <img src="../../assets/logo.png" alt="">
                    <span class="name">{{'邓草草'}}</span>
                </p>
                <ul class="nav_router" ref="ul_li">
                    <router-link to="/" tag="li">
                        首页
                        <div class="music" ref="div0"></div>
                    </router-link>
                    <router-link to="/knowledge" tag="li">
                        前端知识
                        <div class="music" ref="div1"></div>
                    </router-link>
                    <router-link to="/demo" tag="li">
                        demo集锦
                        <div class="music" ref="div2"></div>
                    </router-link>
                    <router-link to="/question" tag="li">
                        前端题库
                        <div class="music" ref="div3"></div>
                    </router-link>
                    <router-link to="/leaveWord" tag="li">
                        留言薄
                        <div class="music" ref="div4"></div>
                    </router-link>
                    <li><i class="iconfont icon-yinle" ref="music" @click="checkMusic"></i></li>
                    <li class="last_li"><i class="iconfont icon-icon-" @click="search"></i></li>
                </ul>
                <audio src="" ref="audio" v-if="isMusic"></audio>
            </div>
            <div class="search"></div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            isMusic:false,
            fun: null
        }
    },
    methods:{
        checkMusic(){
            this.isMusic = !this.isMusic
            this.fun = e => {
                if(e.target.tagName.toLowerCase() === 'li'){
                    switch (e.target.innerText) {
                        case '首页':
                            this.$refs['audio'].src = 'audio/1.mp3'
                            this.play(this.$refs['audio'])
                            break;
                        case '前端知识':
                            this.$refs['audio'].src = 'audio/2.mp3'
                            this.play(this.$refs['audio'])
                            break;
                        case 'demo集锦':
                            this.$refs['audio'].src = 'audio/3.mp3'
                            this.play(this.$refs['audio'])
                            break;
                        case '前端题库':
                            this.$refs['audio'].src = 'audio/4.mp3'
                            this.play(this.$refs['audio'])
                            break;
                        case '留言薄':
                            this.$refs['audio'].src = 'audio/5.mp3'
                            this.play(this.$refs['audio'])
                            break;
                        default:
                            break;
                    }
                }
            }
            if(this.isMusic){
                this.$refs['music'].classList = 'iconfont icon-xin-yinfu'
                for(let i = 0;i<5;i++){
                    this.$refs['div'+i].style.opacity = 1
                }
                this.$refs['ul_li'].addEventListener('mouseover',this.fun)
                return
            }
            this.$refs['music'].classList = 'iconfont icon-yinle'
            for(let i = 0;i<5;i++){
                this.$refs['div'+i].style.opacity = 0
            }
            this.$refs['ul_li'].removeEventListener('mouseover',this.fun)
        },
        play(audio){
            audio.oncanplay = function(){
                audio.play()
            }
        },
        search(){

        }
    }
}
</script>
<style scoped>
    .fl{
        float: left;
    }
    .fr{
        float: right;
    }
    .login_register{
        height: 40px;
        background-color: black;
    }
    .login_register>.contniear{
        width: 1200px;
        height: 100%;
        color: #fff;
        margin: 0 auto;
        line-height: 40px;
        font-size: 14px;
    }
    .contniear>p{
        cursor: pointer;
    }
    .lr>:nth-child(1){
        margin-right: 10px;
    }
    .nav{
        position: relative;
        width: 100%;
        height: 60px;
        background-image: linear-gradient(rgb(254,254,254),rgb(229,229,229));
        box-shadow: 0 4px 10px #D4D4D4;
    }
    .nav>.search{
        position: absolute;
        left: 0;
        top: 60px;
        width: 100%;
        height: 160px;
        background-color: rgba(255,255,255,0.5);
    }
    .nav>.nav_contniear{
        display: flex;
        justify-content: space-between;
        width: 1200px;
        height: 100%;
        margin-left: 50%;
        transform: translateX(-50%);
    }
    .nav_contniear>.logo{
        line-height: 60px;
    }
    .logo>.name{
        margin-left: 20px;
    }
    .nav_contniear>.logo>img{
        width: 40px;
        vertical-align: middle;
    }
    .nav_contniear>.logo>span{
        vertical-align: middle;
    }
    .nav_contniear>.nav_router{
        height: 100%;
        overflow: hidden;
    }
    .nav_contniear>.nav_router>li{
        position: relative;
        float: left;
        line-height: 60px;
        padding: 0 10px;
        cursor: pointer;
        font-size: 14px;
    }
    .nav_contniear>.nav_router>.last_li{
        padding-right: 0;
    }
    .nav_router .icon-yinle,
    .nav_router .icon-xin-yinfu,
    .nav_router .icon-icon-{
        font-size: 24px;
    }
    .nav_router>li>.music{
        position: absolute;
        left: 0;
        bottom:0;
        width: 100%;
        height: 6px;
        opacity: 0;
    }
    .nav_router>li:nth-child(1)>.music{
        background-color: rgb(204,222,108);
    }
    .nav_router>li:nth-child(2)>.music{
        background-color: rgb(209,238,246);
    }
    .nav_router>li:nth-child(3)>.music{
        background-color: rgb(246,216,209);
    }
    .nav_router>li:nth-child(4)>.music{
        background-color: rgb(151,206,243);
    }
    .nav_router>li:nth-child(5)>.music{
        background-color: rgb(251,197,142);
    }
    .router-link-active{
        color: skyblue !important;
    }
</style>