<template>
    <div class="login">
        <div class="form">
            <img src="../../assets/logo.png" alt="">
            <el-form ref="ruleForm" class="demo-ruleForm" :rules="rules" :model="ruleForm">
                <el-form-item  prop="user">
                    <el-input type="text" prefix-icon="el-icon-user" placeholder="用户名" v-model="ruleForm.user"></el-input>
                </el-form-item>
                <el-form-item prop="pass">
                    <el-input type="password" prefix-icon="el-icon-lock" placeholder="密码" v-model="ruleForm.pass"></el-input>
                </el-form-item>
                <p>忘记密码?</p>
                <el-form-item>
                    <el-button type="primary" @click="submit('ruleForm')">登录</el-button>
                </el-form-item>
            </el-form>
            <p @click="href">账号注册?</p>
        </div>
    </div>
</template>
<script>
import loginApi from '../../api/login'
export default {
    data() {
        return {
            ruleForm:{
                user: '',
                pass: ''
            },
            rules: {
                user:[
                    { required: true, message: '请输入用户名', trigger: 'blur' },
                    { min: 2, max: 5, message: '长度在 2 到 5 个字符', trigger: 'blur' }
                ],
                pass:[
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 11, max: 16, message: '长度在 11 到 16 个字符', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        submit(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    loginApi.login(this.ruleForm).then(res => {
                        if(res.status === 200){
                            this.noticeMsg(res)
                            this.resetForm(formName)
                            this.$router.push('/')
                            this.$notify({
                                title: `欢迎${res.data.nick}`,
                                type: 'success'
                            });
                        }
                        if(res.status === 202){
                            this.noticeMsg(res)
                        }
                    })
                } else {
                    return false;
                }
            });
        },
        noticeMsg(resData){
            this.$message({
                showClose: true,
                message: resData.data.msg,
                type: resData.status === 200 ? 'success':'error'
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 注册路由跳转
        href(){
            this.$router.push('/register')
        }
    },
}
</script>
<style scoped>
    .login{
        position: relative;
        width: 100%;
        height: 100%;
        background-image: url('../../assets/login_bg.jpg');
        background-size: cover;
    }
    .form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 360px;
        background-color: rgba(0, 0, 0, 0.5);
    }
    .form>img{
        border-radius: 50%;
        margin: 25px 0;
    }
    .el-form{
        width: 70%;
    }
    .el-form p,
    .form p{
        width: 100%;
        text-align: right;
        font-size: 12px;
        color: #fff;
        cursor: pointer;
    }
    .form p{
        margin-right: 20px;
        margin-bottom: 8px;
    }
    .el-form-item>>>.el-input__inne,
    .el-button{
        border-radius: 3px;
    }
    .el-button{
        width: 100%;
        margin-top: 10px;
    }
</style>