<template>
    <div class="home">
        <el-carousel trigger="click" height="360px">
            <el-carousel-item v-for="item in swiper" :key="item.id">
                <img :src="item.banner" alt="">
            </el-carousel-item>
        </el-carousel>
        <div class="container clearfix">
            <div class="news fl"></div>
            <div class="hotTop fr">
                <ul class="wbhot">
                    <h3 class="title"><i class="iconfont icon-resou"></i>微博实时热搜排行</h3>
                    <li v-for="item in wbhot" :key="item.id">
                        <el-tooltip class="item" effect="light" :content="item.content" placement="bottom-end">
                            <p class="content">{{item.content}}</p>
                        </el-tooltip>
                        <el-tooltip class="item" effect="light" :content="item.num" placement="bottom-end">
                           <p class="num"><i class="iconfont icon-liulanliang" v-if="item.id !== 0"></i>{{item.num}}</p>
                        </el-tooltip>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import request from '../utils/request'
import homeApi from '../api/home'
export default {
    data() {
        return {
            swiper:null,
            wbhot:null
        }
    },
    created() {
        // 获取轮播数据
        request.get('/swiper').then(res => {
            this.swiper = res.data
        })
        homeApi.home().then(res => {
            this.wbhot = res.data
        }).catch(err => {
            console.log(err)
        })
    }
}
</script>
<style scoped>
    .fl{
        float: left;
    }
    .fr{
        float: right;
    }
    .clearfix::after{
        content: "";
        display: block;
        clear: both;
    }
    .el-carousel{
        width: 100%;
        margin: 20px auto;
    }
    .container>.news{
        width: 846px;
        height: 300px;
        box-shadow: 0 0 8px #969696;
        background-color: chartreuse;
    }
    .container>.hotTop{
        width: 328px;
        box-shadow: 0 0 8px #969696;
        background-color: darkkhaki;
    }
    .wbhot>.title{
        padding: 0 20px;
        font-size: 18px;
        font-weight: 400;
        line-height: 40px;
        border-bottom: 1px solid #ccc;
    }
    .title>.icon-resou{
        font-size: 24px;
        margin-right: 8px;
        color: #d81e06;
    }
    .wbhot>li{
        display: flex;
        padding: 0 20px;
        font-size: 16px;
        line-height: 30px;
    }
    .wbhot>li>.content{
        width: 75%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor:context-menu;
    }
    .wbhot>li>.num{
        width: 25%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: 14px;
        cursor:context-menu;
    }
    .wbhot>li .icon-liulanliang{
        font-size: 22px;
        font-weight: 600;
        margin:0 8px;
    }
</style>